.root fieldset{display:grid;grid-template-columns:1fr 1fr;column-gap:20px;row-gap:14px;margin-bottom:24px}@media(max-width: 960px){.root fieldset{row-gap:12px;margin-bottom:20px}.root fieldset button{text-align:center}}.root input{width:100%;background:var(--white);border:1px solid var(--tan);border-radius:10px;padding:11px 20px;font-size:16px;line-height:26px}.root input::placeholder{font-family:var(--font-family)}.root input:focus{outline:1px solid var(--tan);box-shadow:0 2px 4px var(--tan)}.root input:nth-child(3){grid-column-start:1;grid-column-end:3}@media(max-width: 960px){.root input{padding:10px 16px;font-size:14px;line-height:22px}}.root__note{font-weight:300;font-size:14px;line-height:20px;margin-bottom:30px}.root__price{position:relative;display:block;grid-column-start:1;grid-column-end:3}.root__price input{padding-right:40px}.root__price::before{content:" ";position:absolute;font-weight:500;font-size:18px;line-height:100%;top:50%;transform:translateY(-50%);right:20px}